@import url(https://fonts.googleapis.com/css?family=Audiowide);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-bg-color: linear-gradient(56deg, rgba(2,0,36,1) 0%, rgba(205,205,207,1) 55%);
  --main-bg-page: linear-gradient(0deg, rgba(217,224,211,1) 30%, rgba(231,231,247,1) 69%);
  --main-bg-gray:radial-gradient(circle, rgba(116,119,113,1) 0%, rgba(179,217,186,1) 100%);
}

#root {
  height: 100%;
  font-family: 'Audiowide';
}

.app {
  background: linear-gradient(56deg, rgba(2,0,36,1) 0%, rgba(205,205,207,1) 55%);
  background: var(--main-bg-color);
  height: 100%;
  width: 100%;
  position: absolute;
  min-height: 1100px;
  overflow: auto;
  scroll-behavior: smooth;
}

