}

.left-container p{
  font-weight: bold;
}

/* @media screen and (max-width: 500px) {
  .left-container {
    width: 100%;
  }
} */

@media screen and (max-width: 750px) {
  .left-container {
    width: 100%;
  }
}

.main-container {
  padding: 10px;
  margin-bottom: 50px;
  width: 100%;
}

.portfolio-list{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin: 0;
}

.back-to-top-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 85vh;
  padding: 10px;
  text-align:right; 
  margin:0px 10px 0px auto;
  width:50px;
  z-index: 1;
}

.back-to-top-wrapper .icon {
