    padding: 0;
    width: 0;
  }
}

.item-images {
  width: 90%;
  max-height: 650px;
  padding: 20px;
  margin: 10px;
  /* overflow: auto; */
  overflow: hidden;
  white-space: nowrap;
}

div.item-images img {
  max-width: 100%;
  max-height: 450px;
  object-fit: contain;
}

.item-images ul{
  -webkit-padding-start:0 !important;
          padding-inline-start:0 !important;
}

@media screen and (max-width: 750px) {
  .item-images {
    height: auto;
  }
}

.portfolio-item {
  display: flex;
  justify-content: space-around;
  background-color: #f1f1f1;
  margin: 20px;
  width: 80%;
  border-radius: 5px;
}

.portfolio-item:hover {
  box-shadow: 5px 10px 8px 10px rgba(33,33,33,.2);
  transform: scale(1.05);

  -webkit-transition: transform 0.5s ease-in-out;
  -moz-transition:transform 0.5s ease-in-out;
  -ms-transition:transform 0.5s ease-in-out;
}

.image-block {
  width: 50%;
}

.desc {
  width: 50%;
  padding: 30px 30px 30px 5px;
}

.item-name{
  font-weight: 600;
}

.item-link{
  margin-top: 10px;
}

.item-description p:first-child{
  text-decoration: underline;
}

.item-description p:nth-child(2){
  font-size: small;
}

.item-tools p {
  text-decoration: underline;
}

